.workShop-container {
  height: 1080px;
  width: 100vw;
  display: flex;
  background-size: cover;
  background-position: center center;
  transition: all 300ms ease;
  position: absolute;
}

.workShop-backgroupd-container {
  background-color: #dd1a1e;
  height: 1080px;
  width: 100vw;
  display: flex;
}

.workShop-right {
  flex: 0.6 1;
  display: flex;
  z-index: 1;
}

.colum-1 {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
}

.colum-2 {
  flex: 0.5 1;
  display: flex;
  align-items: center;
  /* justify-content: center; */
  padding-left: 20px;
}

.workShop-left {
  z-index: 1;
  flex: 0.4 1;
  display: flex;
  flex-direction: column;
  margin-top: 250px;
}

.workShop-title {
  margin-bottom: 50px !important;
  text-transform: uppercase;
  font-weight: 700 !important;
}

.workShop-text {
  width: 30vw;
}

.slideshowWorkshop-content {
  display: none;
}

.whorkshop-text-container {
  display: flex;
  flex-direction: column;
}

@media (max-width: 900px) {
  .workShop-backgroupd-container {
    background-color: #dd1a1e;
    height: 1080px;
    width: 100vw;
    display: flex;
    flex-direction: column-reverse;
  }

  .workShop-left {
    z-index: 1;
    flex: 0.2 1;
    display: flex;
    flex-direction: column;
    margin-top: 115px;
    margin-left: 200px;
  }

  .workShop-right {
    flex: 0.8 1;
    display: flex;
    z-index: 1;
  }

  .workShop-text {
    width: 40vw;
  }

  .colum-1 {
    flex: 0.5 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-end;
  }

  .colum-2 {
    flex: 0.5 1;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    padding-left: 0px;
  }

  .slideshowWorkshop-content {
    display: block;
  }

  .slick-slide.slick-active.slick-current {
    display: flex;
    justify-content: center;
  }

  .workShop-right {
    display: none;
  }

  .workShop-left {
    z-index: 1;
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-top: 0px;
    margin-left: 0px;
    justify-content: center;
  }

  .slick-slide.slick-active.slick-current {
    display: flex;
  }
  .slideshowWorkshop-content {
    margin-top: 50px;
  }

  .whorkshop-text-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 500px) {
  .workShop-right {
    flex: 1;
    display: none;
    z-index: 1;
    flex-direction: column;
    align-items: center;
  }

  .workShop-backgroupd-container {
    background-color: #dd1a1e;
    height: auto;
    width: 100vw;
    display: flex;
    flex-direction: column-reverse;
    padding: 80px 0 80px 0;
  }

  .workShop-left {
    z-index: 1;
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-top: 0px;
    /*  margin-left: 15%; */
  }
  .workShop-text {
    width: 60vw;
  }

  .carruselWorkshop-slide {
    width: 400px;
    height: 300px;
  }

  .slideshowWorkshop-content {
    width: 400px;
    height: 300px;
  }

  .slick-slide.slick-active.slick-current {
    display: flex;
  }
}

@media (max-width: 497px) {
  .slideshowWorkshop-content {
    width: 100%;
    height: 350px;
  }

  /* .slick-slide.slick-active.slick-current {
  display: flex;
  justify-content: center;
} */
  .carruselWorkshop-slide {
    width: 100%;
    height: 350px;
  }
}
