.roundTable-container {
  height: 900px;
  width: 100vw;
  background-color: #0c2372;
  padding-bottom: 258px;
}

.roundTable-textContainer {
  padding: 70px;
  display: flex;
  flex-direction: row;
  margin-left: 130px;
}

.relleno {
  flex: 0.5 1;
}

.textContainer {
  flex: 0.5 1;
}

.textContainer {
  width: 500px;
  margin-top: 35px;
}

.roundTable-carrouselContainer {
  height: auto;
  overflow: hidden;
}

@media (max-width: 800px) {
  .roundTable-textContainer {
    padding: 100px;
    display: flex;
    flex-direction: column;
    margin-left: 0px;
  }
}

@media (max-width: 600px) {
  .textContainer-text {
    width: 350px;
  }

  .textContainer {
    width: 410px;
    margin-top: 35px;
  }
}

@media (max-width: 500px) {
  .roundTable-textContainer {
    padding: 60px 45px 60px 45px;
    display: flex;
    flex-direction: column;
    margin-left: 0px;
  }

  .textContainer {
    width: 310px;
    margin-top: 35px;
  }
  
}


@media (max-width: 350px) {
  .roundTable-textContainer {
    padding: 30px 30px 70px 30px;
    display: flex;
    flex-direction: column;
    margin-left: 0px;
  }

  .textContainer {
    width: 250px;
    margin-top: 35px;
  }
}
